import Avatar from '@mui/material/Avatar';
import { Container } from '@mui/material';
import WorkReference from './WorkReference';
import Educations from './Education';
import Contact from './Contact';
import Skills from './Skills';
import Badges from './Badges';
import AOS from 'aos';
import {useEffect} from "react";
import "aos/dist/aos.css";


export default function Perfiles() {
    
    useEffect( ()=>{
        AOS.init();
        AOS.refresh();
    },[] )

    return(
        <div style={{ overflow:'hidden', paddingTop: '3%' }}>
            <br></br>
            <div>
                <div data-aos="flip-down" >
                    <Container maxWidth="lg" sx={{ display:'flex', boxShadow:'0 2px 5px 0 rgba(0,0,0,.25),0 3px 10px 0 rgba(0,0,0,.2)!important' }} >
                        <div style={{ width:'30%', display:'contents'  }}>
                            <Avatar alt="Remy Sharp"
                                src="https://www.grupodigital.eu/blog/wp-content/uploads/2019/11/gamer-decorative-illustration-flat-design_23-2148250394-e1574340136197.jpg"
                                sx={{ width: 200, height: 200}}
                            />
                        </div>
                        <div style={{ width:'20%' }}></div>
                        <div style={{ width:'60%', display:'contents' }}>
                            <h3>{'Lorem Ipsum es simplemente el texto de relleno de las imprentas y archivos de texto. Lorem Ipsum ha sido el texto de relleno estndar de las industrias desde el ao 1500, cuando un impresor (N. del T. persona que se dedica a la imprenta) desconocido us una galera de textos y los mezcl de tal manera que logr hacer un libro de textos especimen. No slo sobrevivi 500 aos, sino que tambien ingres como texto de relleno en documentos electrnicos, quedando esencialmente igual al original. Fue popularizado en los 60s con la creacin de las hojas "Letraset", las cuales contenian pasajes de Lorem Ipsum, y ms recientemente con software de autoedicin, como por ejemplo Aldus PageMaker, el cual incluye versiones de Lorem Ipsum.'}</h3>
                        </div>
                    </Container>
                </div>
                { /*
                <br/>
                <Container maxWidth="lg" sx={{ display:'content', boxShadow:'0 2px 5px 0 rgba(0,0,0,.25),0 3px 10px 0 rgba(0,0,0,.2)!important'}} >
                <Skills/>
                </Container>
                */ }
                <br/>
                <div data-aos="fade-left" data-aos-delay="70">
                    <Container maxWidth="lg" sx={{ display:'content', boxShadow:'0 2px 5px 0 rgba(0,0,0,.25),0 3px 10px 0 rgba(0,0,0,.2)!important'}} >
                        <WorkReference />
                    </Container>
                </div>
                <br/>
                <div data-aos="fade-right">
                    <Container maxWidth="lg" sx={{ display:'content', boxShadow:'0 2px 5px 0 rgba(0,0,0,.25),0 3px 10px 0 rgba(0,0,0,.2)!important'}} >
                        <Educations />
                    </Container>
                </div>
                <br/>
                <div data-aos="zoom-in" data-aos-duration="1000">
                    <Container maxWidth="lg" sx={{ display:'flex', boxShadow:'0 2px 5px 0 rgba(0,0,0,.25),0 3px 10px 0 rgba(0,0,0,.2)!important' }} >
                        <Badges />
                    </Container>
                </div>
                <br/>
                <div data-aos="fade-up">
                    <Container maxWidth="lg" sx={{ display:'flex', boxShadow:'0 2px 5px 0 rgba(0,0,0,.25),0 3px 10px 0 rgba(0,0,0,.2)!important', height:'420px' }} >
                        <Contact />
                    </Container>
                </div>
            </div>
        </div>
    )
}